import React, { FC, Suspense } from "react";
import { Spin } from "antd";

/**
 * @description 
 * @param {Element} Comp 
 * @returns element
 */
const lazyLoad = (Comp: React.LazyExoticComponent<FC<{}>>): React.ReactNode => {
	return (
		<Suspense
			fallback={
				<Spin
					size="large"
					style={{
						display: "flex",
						alignItems: "center",
						justifyContent: "center",
						height: "100%"
					}}
				/>
			}
		>
			<Comp />
		</Suspense>
	);
};

export default lazyLoad;
