import { Layout, Select } from "antd";
import AvatarIcon from "./components/AvatarIcon";
import Fullscreen from "./components/Fullscreen";
import Logo from "@/assets/images/logo.png";
import "./index.less";
import { useLocation, useNavigate, useSearchParams } from "react-router-dom";
import { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { GlobalState } from "@/store/interface";
import Docs from "@/components/Docs";
import { getAppList } from "@/api/modules/app";
import { App } from "@/api/interface/app";
const { Header } = Layout;

const LayoutHeader = () => {
	const { pathname } = useLocation();
	const global: GlobalState = useSelector((state: any) => state.global);
	const [showApp, setShowApp] = useState(false);
	const [showAppList, setShowAppList] = useState(false);
	const [appList, setAppList] = useState([]);
	const [currentApp, setCurrentApp] = useState<App.ResApp>();
	const [showDoc, setShowDoc] = useState(false);
	const navigate = useNavigate();
	const [searchParams] = useSearchParams();

	useEffect(() => {
		setShowApp(!pathname.endsWith('home/app'));
		if(!pathname.endsWith('home/app')){
			initAppList();
		}else{
			setShowAppList(false);
		}
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [pathname]);

	const goHome = () => {
		navigate('/home/app');
	}

	const initAppList = async () => {
		const appId = searchParams.get('appId');
		const result = await getAppList({ pageNum: 1, pageSize: 100 });
		if (result.data) {
			const list = result.data.data.map(item => {
				if (item.appId === appId) {
					setCurrentApp(item);
				}
				return { value: item.appId, label: item.name }
			})
			setAppList(list as []);
			setShowAppList(true);
		}
	};

	const openDoc = () => {
		setShowDoc(true);
	}

	const closeDoc = () => {
		setShowDoc(false);
	}

	const handleChangeApp = (value: string) => {
		navigate(`${pathname}?appId=${value}`);
		window.location.reload();
	};

	return (
		<Header>
			<div className="header-lf">
				<img className="logo" src={Logo} alt="log" />
				<span className="title"></span>
				{showApp && <span className="menu" onClick={goHome}></span>}
				{showAppList && <Select
					defaultValue={currentApp?.appId}
					className="menu"
					style={{ width: 120 }}
					onChange={handleChangeApp}
					options={appList}
				/>}

			</div>
			<div className="header-ri">
				<span className="menu" onClick={openDoc}></span>
				<Fullscreen />
				<span className="username">{global.userInfo.loginName}</span>
				<AvatarIcon />
				<Docs open={showDoc} onClose={closeDoc}></Docs>
			</div>
		</Header>
	);
};

export default LayoutHeader;
