import Draggable from 'react-draggable';
import './index.less'
import { Button } from 'antd';
import { CloseOutlined, CompressOutlined, ExpandOutlined } from '@ant-design/icons';
import { useState } from 'react';

type DocsProps = {
    open: boolean;
    onClose?: () => void;
}

const Docs: React.FC<DocsProps> = ({ open, onClose }: DocsProps) => {
    const [isFullScreen, setIsFullScreen] = useState(false);
    const url = 'https://gray-whale.github.io/owljs/';
    const close = () => {
        if (onClose) {
            onClose();
        }
    }
    const showFullScreen = () => {
        setIsFullScreen(true);
    }
    const hideFullScreen = () => {
        setIsFullScreen(false)
    }

    if (!open) return (<div style={{ display: 'none' }}></div>);
    return (<div className="doc-container">
        <div className="floating-root">
            <Draggable>
                <div className={`doc ${isFullScreen ? 'fullscreen' : 'shrink'}`}>
                    <div className='doc-center'>
                        <div className='doc-header'>
                            <div className='justify-grid'>
                                <span className='left'></span>
                                <div className='right'>
                                    {isFullScreen ? <CompressOutlined onClick={hideFullScreen} className='icon' /> : <ExpandOutlined className='icon' onClick={showFullScreen} />}
                                    <CloseOutlined onClick={close} />
                                </div>
                            </div>
                        </div>
                        <div className='doc-body'>
                            <iframe referrerPolicy="unsafe-url" title="navigation" src={url} />
                        </div>
                        <div className='doc-footer'>
                            <Button type="primary" href={url} target='_blank' block></Button>
                        </div>
                    </div>
                </div>
            </Draggable>
        </div>
    </div>);
}
export default Docs;