import axios, { AxiosError, AxiosInstance, AxiosRequestConfig, AxiosResponse } from "axios";
import { checkStatus } from "./helper/checkStatus";
import { message } from "antd";
import { store } from "@/store";
import { ResultData } from "./interface";
import { ResultEnum } from "@/enums/httpEnum";
import NProgress from "@/config/nprogress";
import { setToken } from "@/store/modules/global/action";
// import { AxiosCanceler } from "./helper/axiosCancel";

// const axiosCanceler = new AxiosCanceler();

const config = {
	//  .env 
	baseURL: process.env.REACT_APP_BASE_API,
	// (10s)
	timeout: 10000,
	// 
	// withCredentials: true
};

class RequestHttp {
	service: AxiosInstance;
	public constructor(config: AxiosRequestConfig) {
		// axios
		this.service = axios.create(config);

		/**
		 * @description 
		 *  -> [] -> 
		 * token(JWT) : token,redux/
		 */
		this.service.interceptors.request.use(
			(config: AxiosRequestConfig) => {
				NProgress.start();
				// *  pending 
				// axiosCanceler.addPending(config);
				const token: string = store.getState().global.token;
				return { ...config, headers: { ...config.headers, "Authorization": `Bearer ${token}` } } as any;
			},
			(error: AxiosError) => {
				console.log('request-------error');
				message.error('');
				// window.location.pathname = "/login";
			}
		);

		/**
		 * @description 
		 *   -> [] -> JS
		 */
		this.service.interceptors.response.use(
			(response: AxiosResponse) => {
				const { data } = response;
				NProgress.done();
				// * (loading)
				// axiosCanceler.removePending(config);
				// * 401
				if (data.code === ResultEnum.OVERDUE) {
					store.dispatch(setToken(""));
					message.error(data.msg);
					// window.location.pathname = "/login";
					return;
				}
				// * code
				if (data.code && data.code !== ResultEnum.SUCCESS) {
					message.error(data.msg);
					return;
				}
				// * 
				return data;
			},
			async (error: AxiosError) => {
				const { response } = error;
				NProgress.done();
				//  response
				if (error.message.indexOf("timeout") !== -1) message.error("");
				// 
				if (response) checkStatus(response.status);
				// () :
				if (!window.navigator.onLine) window.location.pathname = "/500";
				console.log('response-------error');
				message.error('');
				// window.location.pathname = "/login";
			}
		);
	}

	// * 
	get<T>(url: string, params?: object, _object = {}): Promise<ResultData<T>> {
		return this.service.get(url, { params, ..._object });
	}
	post<T>(url: string, params?: object, _object = {}): Promise<ResultData<T>> {
		return this.service.post(url, params, _object);
	}
	put<T>(url: string, params?: object, _object = {}): Promise<ResultData<T>> {
		return this.service.put(url, params, _object);
	}
	delete<T>(url: string, params?: any, _object = {}): Promise<ResultData<T>> {
		return this.service.delete(url, { params, ..._object });
	}
}

// eslint-disable-next-line import/no-anonymous-default-export
export default new RequestHttp(config);