import { useState } from "react";
import { Button, Form, Input, message } from "antd";
import { useNavigate } from "react-router-dom";
import { Login } from "@/api/interface/login";
import { login } from "@/api/modules/login";
import { UserOutlined, LockOutlined } from "@ant-design/icons";
import { useDispatch } from "react-redux";
import { setToken, setUserInfo } from "@/store/modules/global/action";

const LoginForm: React.FC = () => {
	const navigate = useNavigate();
	const [form] = Form.useForm();
	const dispatch = useDispatch();
	const [loading, setLoading] = useState<boolean>(false);

	// 
	const onFinish = async (loginForm: Login.ReqLoginForm) => {
		try {
			setLoading(true);
			// loginForm.password = md5(loginForm.password);
			const { data } = await login(loginForm);
			dispatch(setToken(data!.access_token));
			console.log('');
			dispatch(setUserInfo(data!.userInfo));
			message.success("");
			navigate('/home/app', { replace: true });
		} catch (e) {
			console.log(e)
		} finally {
			setLoading(false);
		}
	};

	const onFinishFailed = (errorInfo: any) => {
		console.log("Failed:", errorInfo);
	};

	return (
		<Form
			form={form}
			name="basic"
			labelCol={{ span: 5 }}
			initialValues={{ remember: true }}
			onFinish={onFinish}
			onFinishFailed={onFinishFailed}
			size="large"
			autoComplete="off"
		>
			<Form.Item name="loginName" rules={[{ required: true, message: "" }]}>
				<Input placeholder="" prefix={<UserOutlined />} />
			</Form.Item>
			<Form.Item name="password" rules={[{ required: true, message: "" }]}>
				<Input.Password autoComplete="new-password" placeholder="" prefix={<LockOutlined />} />
			</Form.Item>
			<Form.Item className="login-btn">
				<Button type="primary" htmlType="submit" loading={loading} icon={<UserOutlined />}>
					
				</Button>
			</Form.Item>
		</Form>
	);
};
export default LoginForm;
