import { useRef } from "react";
import { Avatar, Modal, Menu, Dropdown, message } from "antd";
import { ExclamationCircleOutlined, LogoutOutlined, SettingOutlined, UserOutlined } from "@ant-design/icons";
import { useNavigate } from "react-router-dom";
import { setToken } from "@/store/modules/global/action";
import PasswordModal from "./PasswordModal";
import InfoModal from "./InfoModal";
import { useDispatch } from "react-redux";

const AvatarIcon: React.FC = () => {
	const navigate = useNavigate();
	const dispatch = useDispatch();

	interface ModalProps {
		showModal: (params: { name: number }) => void;
	}
	const passRef = useRef<ModalProps>(null);
	const infoRef = useRef<ModalProps>(null);

	// 
	const logout = () => {
		Modal.confirm({
			title: "",
			icon: <ExclamationCircleOutlined />,
			content: "",
			okText: "",
			cancelText: "",
			onOk: () => {
				dispatch(setToken(""));
				message.success("");
				navigate("/login");
			}
		});
	};

	// Dropdown Menu
	const menu = (
		<Menu
			items={[
				{
					key: "1",
					icon: <UserOutlined />,
					label: <span className="dropdown-item"></span>,
					onClick: () => infoRef.current!.showModal({ name: 11 })
				},
				{
					key: "2",
					icon: <SettingOutlined />,
					label: <span className="dropdown-item"></span>,
					onClick: () => passRef.current!.showModal({ name: 11 })
				},
				{
					key: "3",
					icon: <SettingOutlined />,
					label: <span className="dropdown-item"></span>,
					onClick: () => navigate("/home/system")
				},
				{
					type: "divider"
				},
				{
					key: "4",
					icon: <LogoutOutlined />,
					label: <span className="dropdown-item"></span>,
					onClick: logout
				}
			]}
		></Menu>
	);
	return (
		<>
			<Dropdown overlay={menu} placement="bottom" arrow trigger={["click"]}>
				<Avatar style={{ backgroundColor: '#c0c4cc' }} size="large" icon={<UserOutlined />} />
			</Dropdown>
			<InfoModal innerRef={infoRef}></InfoModal>
			<PasswordModal innerRef={passRef}></PasswordModal>
		</>
	);
};

export default AvatarIcon;
